.toggle {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    display: none;
    position: absolute;
    top: 0;
    left: 1.5rem;
    bottom: 0;
    width: 2rem;
    height: 1.4rem;
    background: transparent;
    padding: 0;
    margin: auto;
    border: 0;
    cursor: pointer;
    -webkit-transition: all ease 0.5s;
    transition: all ease 0.5s;
    z-index: 4
}

.toggle::before,
.toggle::after {
    content: "";
    position: absolute;
    width: inherit;
    height: 0.2rem;
    background: #00a87b;
    -webkit-transition: all ease 0.5s;
    transition: all ease 0.5s
}

.toggle::before {
    top: 0
}

.active.toggle::before {
    top: 50%;
    margin-top: -0.1rem;
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg)
}

.toggle::after {
    bottom: 0
}

.active.toggle::after {
    bottom: 50%;
    margin-bottom: -0.1rem;
    -webkit-transform: rotate(135deg);
    transform: rotate(135deg)
}

