.slick-arrow.slick-prev,
.slick-arrow.slick-next {
    font-size: 0;
    line-height: 0;
    position: absolute;
    top: -60px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    width: 4rem;
    height: 4rem;
    padding: 0;
    -webkit-transform: translate(0, -50%);
    -ms-transform: translate(0, -50%);
    transform: translate(0, -50%);
    cursor: pointer;
    color: transparent;
    border: none;
    outline: none;
    background: #000;
    border-radius: 75%;
    z-index: 2;
}

#serve .slick-arrow {
    position: absolute;
    background: #fff;
    -webkit-transform: none;
    transform: none;
    -webkit-box-shadow: 0 1rem 4rem -0.3rem rgb(6 3 62 / 10%);
    box-shadow: 0 1rem 4rem -0.3rem rgb(6 3 62 / 10%);
}

#serve .slick-arrow.slick-prev:after,
#serve .slick-arrow.slick-next:after {
    width: 1.2rem !important;
    height: 1.2rem !important;
    background: #06033e;
    margin: 0 auto
}

