.slick-carousel .owl-carousel .owl-nav button.owl-prev,
.slick-carousel .owl-carousel .owl-nav button.owl-next {
    position: static;
    background: #fff !important;
    -webkit-transform: none;
    transform: none;
    -webkit-box-shadow: 0 1rem 4rem -0.3rem rgb(6 3 62 / 10%);
    box-shadow: 0 1rem 4rem -0.3rem rgb(6 3 62 / 10%);
    width: 4rem;
    height: 4rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    border-radius: 75%;
    color: #06033e;
    font-size: 25px;
    font-weight: bold;
    margin: 0;
    line-height: 1.5;
}

.slick-carousel .owl-carousel .owl-nav {
    display: flex;
    flex-flow: wrap;
    row-gap: 1.5rem;
    column-gap: 1.5rem;
    position: absolute;
    top: -80px;
    right: 186px;
    margin: 0
}

